input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

input[type=radio]:checked + label,
input[type=radio]:focus + label {
  color: #102654;
}

input[type=radio]:checked + label::after {
  content: "";
  width: 100%;
  height: 0.13rem;
  background-color: #d10a42;
  position: absolute;
  bottom: 0;
  left: 0;
}

.subscription__tabs-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 1.5rem;
  justify-content: space-between;
