  flex-direction: column;
  align-items: center;
  position: relative;
  border: 7px solid transparent;
}
.subscription__item:hover {
  border: 7px solid;
  border-image: linear-gradient(to left top, #00359F, #D10A42);
  border-image-slice: 1;
}
.subscription__item:hover .subscription__link {
  color: #ffffff;
  background: linear-gradient(97.05deg, #ED0233 17.6%, #003EB7 95.31%);
  outline: none;
}
