  .subscription__item {
    padding: 3.4rem 7rem 4rem;
  }
}

.subscription__item--active {
  border: 7px solid;
  border-image: linear-gradient(to left top, #00359F, #D10A42);
  border-image-slice: 1;
  padding: 3.46rem 4.46rem 3.81rem;
}
.subscription__item--active:hover {
  padding: 3.46rem 4.46rem 3.81rem;
}
.subscription__item--active .subscription__link {
  color: #ffffff;
  background: linear-gradient(97.05deg, #ED0233 17.6%, #003EB7 95.31%);
  outline: none;
}

.subscription__item h4 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 1.63em;
  line-height: 1.12em;
  margin: 0 0 0.6rem;
}

.subscription__text {
  margin-bottom: 4rem;
}

.subscription__text span {
  color: #495a7d;
}

.subscription__price {
  position: relative;
  z-index: 2;
  margin-bottom: 2.3rem;
}

.subscription__price p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 3.75em;
  line-height: 150%;
  color: #103AAE;
  margin: 0;
  text-align: start;
  display: flex;
  gap: 0.88rem;
  align-items: center;
  position: relative;
  z-index: 2;
}
.subscription__price p::after {
  content: "";
  background-image: url("../img/svg/ruble.svg");
  background-repeat: no-repeat;
  width: 1.88rem;
  height: 2.63rem;
}

.subscription__price-background {
  position: absolute;
  top: 0;
  bottom: 0;
